<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <!-- Page Content -->
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container text-start">
        <div class="my-2">
          مدیریت زیر محصولات:
          <span class="fs-3 m-2">
            {{ productName }}
          </span>
        </div>

        <div class="row">
          <div class="col-md-4 col-xs-6">
            <div class="form-floating mb-3">
              <select
                class="form-control"
                id="childProductsId"
                v-model="childProductsId"
              >
                <template v-for="item in productsList" :key="item.id">
                  <option v-if="item.type != 'package'" :value="item.id">
                    {{ item.name }}
                  </option>
                </template>
              </select>
              <label for="childProductsId">انتخاب زیر محصول</label>
            </div>
          </div>

          <div class="col-md-4 col-xs-6">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="price"
                v-model="price"
                v-on:keyup.enter="addProductToPackage"
              />
              <label for="price">قیمت</label>
            </div>
          </div>
          <div class="col-md-4 col-xs-6">
            <a
              class="btn btn-primary mt-0 mt-md-3"
              @click="addProductToPackage"
            >
              افزودن به پکیج
            </a>
            <span v-show="showSpin">
              <i class="fas fa-spinner fa-spin mt-3"></i>
            </span>
          </div>
        </div>

        <div v-if="!childProductsList">هیچ زیر محصولی وجود ندارد</div>
        <div v-else>
          <div class="row border-bottom p-3">
            <div class="align-items-center col-1 d-flex">#</div>
            <div class="align-items-center col d-flex">نام</div>
            <div class="align-items-center col d-flex">قیمت</div>
            <div class="align-items-center col d-flex">حذف</div>
          </div>
          <template v-for="(item, index) in childProductsList" :key="item.id">
            <div class="row border-bottom p-3">
              <div class="align-items-center col-1 d-flex">
                {{ index + 1 }}
              </div>
              <div class="align-items-center col d-flex">
                {{ item.child_product.name }}
              </div>
              <div class="align-items-center col d-flex">
                {{ this.utils.monize(item.package_price) }}
              </div>
              <div class="align-items-center col d-flex">
                <a
                  class="btn btn-danger btn-sm"
                  @click="delChildProduct(item.id)"
                >
                  حذف
                </a>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TheSidemenu from "../components/TheSidemenu";
import TheTopmenu from "../components/TheTopmenu";
import ArefApi from "../libs/api.js";
import utils from "../libs/utils.js";

export default {
  name: "Packages",
  created: function () {
    this.utils = new utils();
    this.arefApi = new ArefApi(this);
    this.getProductName();
    this.getAllProducts();
    this.getChildProducts();
  },
  components: {
    TheSidemenu,
    TheTopmenu,
  },
  data() {
    return {
      productId: this.$route.params.productId,
      productName: null,
      childProductsId: null,
      price: null,
      showSpin: false,
      productsList: [],
      childProductsList: null,
    };
  },
  methods: {
    async getProductName() {
      try {
        let res = await this.arefApi.getProduct(this.productId);
        this.productName = res.data.name;
      } catch {
        this.productName = "محصول پیدا نشد";
      }
    },
    addProductToPackage() {
      if (!this.childProductsId) {
        this.$swal.fire({
          text: `زیر محصول را انتخاب نمایید`,
          icon: "error",
          confirmButtonText: "متوجه شدم",
        });
        return false;
      }

      if (!this.price) {
        this.$swal.fire({
          text: `قیمت را وارد کنید`,
          icon: "error",
          confirmButtonText: "متوجه شدم",
        });
        return false;
      }

      this.showSpin = true;
      const obj = {
        products_id: this.productId,
        child_products_id: this.childProductsId,
        price: this.price,
      };
      this.arefApi
        .addProductToPackage(obj)
        .then(() => {
          this.utils.showSuccess(this);
          this.getChildProducts();
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        })
        .finally(()=>{
            this.showSpin = false;
        });
    },
    getAllProducts() {
      this.arefApi
        .getProductsIndex(1, true)
        .then((resp) => {
          this.productsList = resp.data;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    getChildProducts() {
      this.arefApi
        .getChildProducts(this.productId)
        .then((resp) => {
          this.childProductsList = resp.data;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    delChildProduct(productDetailPackagesId) {
      this.$swal
        .fire({
          title: "اخطار حذف",
          text: "آیا حذف انجام شود؟",
          icon: "warning",
          showCancelButton: true,
          cancelButtonText: "خیر",
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "بله حذف انجام شود!",
        })
        .then((value) => {
          if (value.isConfirmed) {
            this.arefApi
              .deleteChlidProduct(productDetailPackagesId)
              .then(() => {
                this.getChildProducts();
              })
              .catch((err) => {
                this.utils.showErrors(this, err);
              });
          }
        });
    },
  },
};
</script>
<style>
.select2-container--default {
  min-width: 300px;
  max-width: 500px;
}
</style>
